@font-face {
  font-family: "Google Sans";
  src: url("./fonts/Google-Sans/GoogleSans-Regular.eot");
  src: url("./fonts/Google-Sans/GoogleSans-Regular.eot?#iefix") format("embedded-opentype"), url("./fonts/Google-Sans/GoogleSans-Regular.woff2") format("woff2"), url("./fonts/Google-Sans/GoogleSans-Regular.woff") format("woff"), url("./fonts/Google-Sans/GoogleSans-Regular.ttf") format("truetype"), url("./fonts/Google-Sans/GoogleSans-Regular.svg#GoogleSans-Regular") format("svg");
