body,
#root {
  font-family: "Google Sans", "Cabin", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: var(--bgcolor);
  color: var(--tcolor);
  font-size: 1rem;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  letter-spacing: 0.025rem;
  word-spacing: 0.25rem;
  line-height: 1.25rem;
  transition: all var(--transition-time) ease-in-out;
  overflow-x: hidden;
  background-attachment: fixed;
  background-size: cover;
}

#root {
  width: 100%;
  height: 100%;
  flex-flow: column;
  background: transparent;
}

a, a:hover, a:visited, a:active, a:focus {
  border: none;
  outline: none;
  text-decoration: none;
  color: inherit;
}

img {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

main {
  width: 100%;
}

button {
  border: none;
  outline: none;
  background: none;
  font: inherit;
  cursor: pointer;
  user-select: none;
}

input,
textarea,
select {
  border: none;
  outline: none;
  background: none;
  font: inherit;
  color: inherit;
  resize: none;
}
input::placeholder,
textarea::placeholder,
select::placeholder {
  user-select: none;
}

[data-title] {
  position: relative;
  font-size: 0.875rem;
}
[data-title]:after {
  content: attr(data-title);
  position: absolute;
  top: 120%;
  left: 10%;
  white-space: nowrap;
  box-shadow: 0px 0px 4px #666;
  background-color: rgba(97, 97, 97, 0.85);
  color: #ffffff;
  padding: 0.15rem 0.55rem;
  margin: 2px;
  padding: 4px 8px;
  border-radius: 4px;
  opacity: 0;
  overflow-wrap: break-word;
  font-weight: 500;
  z-index: 99999;
  visibility: hidden;
  transition: all 0.1s ease-in-out;
}
[data-title]:hover:after {
  opacity: 1;
  transition: all 0.1s ease 1s;
  visibility: visible;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: #f5f5f5;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
  border-radius: 500px;
  background-color: var(--ast-global-color-0);
}

.material-symbols-outlined {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  cursor: pointer;
}

.dispn {
  display: none !important;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-0.25rem);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(0.25rem);
  }
  100% {
    transform: translateY(0);
  }
}

