.typing {
  animation: blink 0.5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-size: inherit;
  padding: 0 0.5rem;
  transition: all 0.5s ease-in-out;
}

@keyframes typeit {
  0% {
    width: 0;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}

